.menuItem {
  display: inline-block;
  padding: 0 50px;
  position: relative;
  transition: background-color 0.2s ease;
  line-height: 50px;
  border-right: 1px solid rgba(178, 178, 178, 0.5); }
  .menuItem:first-child {
    border-left: 1px solid rgba(178, 178, 178, 0.5); }
  .menuItem:hover {
    background-color: rgba(229, 229, 229, 0.5);
    cursor: pointer; }
  .menuItem a {
    position: relative;
    display: block;
    color: #646464;
    font-size: 16px; }
